/* 
0 - 600px:    Phone
600 - 900px:  Tablet portrait
900 - 1200px: Tablet landscape
1200 - 1800p
1800px + :    Big desktop

//Breakpoints
-phone
-tab-port
-tab-land
-big-desktop
*/
@import url("https://fonts.googleapis.com/css2?family=Red+Hat+Display:wght@500;700;900&display=swap");
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
}
@media (max-width: 37.5em) {
  html {
    font-size: 50%;
  }
}
@media (min-width: 37.5625em) and (max-width: 56.25em) {
  html {
    font-size: 50%;
  }
}
@media (min-width: 56.3125em) and (max-width: 75em) {
  html {
    font-size: 56.25%;
  }
}
@media (min-width: 112.5em) {
  html {
    font-size: 75%;
  }
}

body {
  box-sizing: border-box;
  background: #e0e8ff;
}

body {
  font-family: "Red Hat Display", sans-serif;
}

.container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.card {
  width: 400px;
  border-radius: 3rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: #f5f7ff;
  margin-bottom: 3rem;
}

.card-head img {
  display: block;
  max-width: 100%;
  max-height: 100%;
  border-top-left-radius: 3rem;
  border-top-right-radius: 3rem;
}

.card-body {
  text-align: center;
  padding: 1rem;
  font-size: 1.7rem;
}

.card-body h2 {
  padding: 2rem;
  color: #1f2f56;
}

.card-body p {
  padding: 2rem;
  color: #7280a7;
}

.card-body-plan {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #f5f7ff;
  padding: 1rem;
  border-radius: 2rem;
}

.card-body-plan-left {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.card-body-plan-left-center {
  margin-top: 1rem;
}

.card-body-plan-left-center h3 {
  color: #1f2f56;
}

.card-body-plan a {
  color: #3829e0;
}

.card-body-plan a:hover {
  color: #594ddb;
}

.card-foot {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
}

.card-foot button {
  padding: 1rem 2rem;
  width: 80%;
  margin: 1rem;
  border-radius: 1rem;
  border: none;
  outline: none;
  height: 7vh;
}

#proceed {
  background: #3829e0;
  font-weight: 700;
  color: #e0e8ff;
}

#proceed:hover {
  background: #594ddb;
  cursor: pointer;
}

#cancel {
  color: #7280a7;
  font-weight: 700;
  background: transparent;
}

#cancel:hover {
  color: #1f2f56;
  cursor: pointer;
}